import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { Input, CheckboxRadio } from "../../components/FormElements";
import axios from 'axios'
import Swal from 'sweetalert2'

function SignUp() {

    const { register, handleSubmit, formState: { errors }, getValues } = useForm({ mode: 'onTouched' })
    const navigate = useNavigate()

    const onSubmit = async(data) => {
        const { nickname, email, password, tel, gender } = data;
        console.log(data);
        try {
            const res = await axios.post('https://todolist-api.hexschool.io/users/sign_up',{
            email,password,nickname
        })
            Swal.fire({
                title: '!',
                showConfirmButton: false,
                icon: 'success',
            })
            navigate('/')
        } catch (error) {
            Swal.fire({
                title: error.response.data.message,
                showConfirmButton: false,
                icon: 'warning',
            })
        }
        
    }
    return (
        <div className='bg-light pt-5 pb-7 full-height'>
            <div className='container'>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className="mb-3">
                        <Input
                            id='nickname'
                            labelText=''
                            type='text'
                            placeholder=''
                            errors={errors}
                            register={register}
                            rules={{
                                required: '',
                            }}
                        ></Input>
                    </div>

                    <div className="mb-3">
                        <Input
                            id='email'
                            labelText='Email'
                            type='email'
                            placeholder='sample@mail.com'
                            errors={errors}
                            register={register}
                            rules={{
                                required: 'Email ',
                                pattern: {
                                    value: /^\S+@\S+$/i,
                                    message: 'Email ',
                                },
                            }}
                        ></Input>
                    </div>

                    <div className="mb-3">
                        <Input
                            id='password'
                            labelText=''
                            type='password'
                            placeholder='6-18'
                            errors={errors}
                            register={register}
                            rules={{
                                required: '',
                                minLength: {
                                    value: 6,
                                    message: ' 6 '
                                },
                                maxLength: {
                                    value: 12,
                                    message: ' 12 '
                                }
                            }}
                        ></Input>
                    </div>

                    <div className="mb-3">
                        <Input
                            id='checkPassword'
                            labelText=''
                            type='password'
                            placeholder='6-18'
                            errors={errors}
                            register={register}
                            rules={{
                                validate: (match) => {
                                    const password = getValues('password');
                                    return match === password || ''
                                }
                            }}
                        ></Input>
                    </div>

                    <div className="mb-3">
                        <Input
                            id='tel'
                            labelText=''
                            type='tel'
                            errors={errors}
                            register={register}
                            placeholder='10'
                            rules={{
                                required: '',
                                minLength: {
                                    value: 10,
                                    message: ' 10 '
                                },
                                maxLength: {
                                    value: 10,
                                    message: ' 10 '
                                }
                            }}
                        ></Input>
                    </div>

                    <div className="mb-3">
                        <div className='form-label'></div>
                        <div className="d-flex">
                            <CheckboxRadio
                                id='male'
                                name='gender'
                                labelText=''
                                type='radio'
                                value='male'
                                errors={errors}
                                register={register}
                                rules={{
                                    required: '',
                                }}
                            ></CheckboxRadio>
                            <CheckboxRadio
                                id='female'
                                name='gender'
                                labelText=''
                                type='radio'
                                value='female'
                                errors={errors}
                                register={register}
                                rules={{
                                    required: '',
                                }}
                            ></CheckboxRadio>
                        </div>
                    </div>

                    <div className="mb-3">
                        <CheckboxRadio
                            id='agree'
                            name='agree'
                            labelText={<p>
                                <button type="button" className="btn text-primary" style={{textDecoration:'underLine'}}></button>
                                <button type="button" className="btn text-primary" style={{textDecoration:'underLine'}}></button>
                                <button type="button" className="btn text-primary" style={{textDecoration:'underLine'}}></button>
                                
                            </p>}
                            type='checkbox'
                            value='true'
                            errors={errors}
                            register={register}
                            rules={{
                                required: '',
                            }}
                        ></CheckboxRadio>
                    </div>
                    <div className="d-flex justify-content-center">
                        <button type='submit'className='btn btn-primary py-3 px-7 rounded'>
                            
                        </button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default SignUp